  src: url("../fonts/roboto-condensed-v19-latin_cyrillic-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-v19-latin_cyrillic-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/roboto-condensed-v19-latin_cyrillic-700.woff2") format("woff2"), url("../fonts/roboto-condensed-v19-latin_cyrillic-700.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.button {
  padding: 0;
  border-radius: 5px;
  box-sizing: border-box;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #ffffff; }

.triangles {
  display: flex;
  position: relative;
  width: 705px;
  height: 515px; }

.triangles__little {
  position: absolute;
  bottom: 0;
  left: 0; }

.triangles__big {
  position: absolute;
  bottom: 0;
  left: 41px; }

html {
  height: 100%;
  scroll-behavior: smooth; }

body {
  display: flex;
  flex-direction: column;
  position: relative;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: "PT Mono", "Courier", monospace;
  background-color: #ffffff; }

@media (max-width: 1023px) {
  body .header__navigation--opened {
    height: 100vh;
    overflow-y: scroll; } }

.header {
  display: flex;
  justify-content: space-between;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  max-width: 1411px;
  width: 73%;
  min-height: 52px;
  background-color: transparent; }

.header__logo--mobile {
  display: none; }

.header__list {
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none; }

.header__item {
  color: #ffffff; }
  .header__item--mail {
    margin-right: 88px; }
  .header__item--phone {
    margin-right: 38px; }

.header__item a {
  font-size: 16px;
  line-height: 18px;
  color: #ffffff; }

.header__button {
  width: 180px;
  height: 45px;
  border: 1px solid #ffffff;
  font-size: 14px;
  line-height: 16px;
  background-color: transparent; }

@media (max-width: 1023px) {
  .header {
    width: 93.5%; }
  .header__item--mail {
    margin-right: 33px; }
  .header__item--phone {
    margin-right: 0; }
