@media (max-width: 767px) {
  .header {
    width: 90%; }
  .header__icon--main {
    display: none; }
  .header__icon--mobile {
    display: block; }
  .header__item--mail {
    margin-right: 23px; } }

.main {
  display: flex;
  flex-direction: column;
  flex-grow: 1; }

.intro {
