    top: 0;
    left: 377px; } }

@media (max-width: 767px) {
  .intro {
    padding: 90px 0 70px; }
  .intro__text {
    width: auto;
    margin: 196px 15px o; }
  .intro__text h1 {
    font-size: 40px;
    line-height: 47px; }
  .intro__text p {
    font-size: 16px;
    line-height: 25px; }
  .intro__button {
