  font-size: 15px;
  line-height: 16px;
  color: #1b2d37;
  text-transform: uppercase;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 50px; }

.footer__buttons {
