  opacity: 0.5;
  text-transform: uppercase; }

@media (max-width: 1023px) {
  .footer {
    padding: 19px 0 24px; }
    .footer::before {
      position: absolute;
      content: "";
      top: 97px;
      left: 50%;
      transform: translateX(-50%);
      width: 100%;
      height: 1px;
      background-color: #626962; }
    .footer::after {
      bottom: -69px; }
  .footer__wrapper,
  .footer__copyright-list {
