  .footer__folder--logo {
    display: flex;
    justify-content: space-between;
    width: 100%; }
  .footer__folder--nav, .footer__folder--contacts {
    width: 50%; }
  .footer__folder--nav {
    height: auto; }
  .footer__nav-item {
    margin: 0; }
  .footer__logo {
    margin-bottom: 0; }
  .footer__span {
    display: block;
    font-size: 13px;
    line-height: 24px;
    color: #1b2d37;
    opacity: 0.5;
    text-transform: uppercase; } }

@media (max-width: 767px) {
  .footer {
    padding: 0; }
    .footer::before {
      content: none; }
    .footer::after {
      bottom: -123px; }
  .footer__wrapper,
  .footer__copyright-list {
    flex-direction: column;
    width: 90%; }
  .footer__folder {
    position: relative; }
    .footer__folder--logo {
      padding: 46px 0 54px;
      text-align: center; }
  .footer__folder--closed {
