  .footer__folder--opened h3,
  .footer__folder--opened h3 {
    margin-bottom: 36px; }
  .footer__logo {
    margin-bottom: 26px; }
  .footer__buttons {
    display: block;
    position: absolute;
    top: 29px;
    right: 0; }
  .hide {
    display: none; }
  .show {
    display: block; } }
